@import '../../style/variables.scss';

.logo {
	&-container {
		display: flex;
		align-items: center;
		gap: 16px;
		margin: 0;
		text-decoration: none;
		color: $--Black;
	}

	width: 48px;
	height: 48px;

	&-text {
		margin: 0;
		font-size: 18px;
		font-style: normal;
		font-weight: 400;
		line-height: 1.3;
	}
}
